/*about */
.about-me-container {
  margin-top: 8vh;
  /* background-color: white; */
  /* min-height: 100vh; */
  width: 100%;

  display: grid;
  grid-template-rows: 1fr auto 1fr;
  grid-template-columns: 4fr 6fr;

  justify-items: center;

  margin-top: 8vh;
  margin-bottom: 8vh;
}
.abT {
  grid-row: 1;
  grid-column: 1 / span 2;
}

.photo-container {
  padding-left: 5vw;
  padding-right: 5vw;

  width: 100%;
  grid-row: 2;
  grid-column: 1;
  /* width: 40vw; */

  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
}
.photo-frame {
  /* background-color: white; */
  grid-row: 1;
  grid-column: 1;

  display: grid;
  grid-template-columns: 1fr;

  align-self: center;
  justify-self: center;

  /* border: 4px double white; */

  /* box-shadow: rgba(0, 0, 0, 0.3) 0px 1px 1px 0px; */

  /* padding: 10px; */
}

.thats-me {
  font-size: 20px;
  opacity: 0;

  width: 100%;
  text-align: center;

  line-height: 1;

  transform: rotate(-12deg);
}
.thats-me-anima {
  animation: photo-anima 1s linear forwards;
}

.curved-arrow {
  margin-top: -14px;
  opacity: 0;

  line-height: 1.5;

  font-size: 36px;

  color: rgb(24, 24, 24);

  text-indent: 90px;
  width: 100%;
  text-align: center;
}
.curved-arrow-anima {
  animation: photo-anima 1s linear forwards 0.5s;
}

.photo-frame img {
  max-width: 100%;
  height: auto;

  border-radius: 50%;

  align-self: center;
  justify-self: center;
  opacity: 0;

  filter:sepia(15%);
}
.photo-anima {
  animation: photo-anima 1s linear forwards .05s;
}

@keyframes photo-anima {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.p-container {
  grid-row: 2;
  grid-column: 2;

  width: 100%;
  display: grid;
  grid-template-columns: 1fr;

  align-self: center;
  align-content: center;
  justify-items: center;

  /* padding: 10px; */
  padding-right: 5vw;

  max-width: 700px;
}
.p-container * {
  color: rgb(6, 6, 6);

  font-size: 20px;
  font-weight: 400;
  line-height: 1.3;

  text-align: justify;
}

.wave-hand {
  display: inline-block;
  margin-left: 4px;

  transform: rotate(0deg);
  transform-origin: 70% 70%;
}
.wave-hand-animations-class {
  animation: wave 1.9s linear 0.9s;
}
@keyframes wave {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(20deg);
  }
  20% {
    transform: rotate(-10deg);
  }
  30% {
    transform: rotate(15deg);
  }
  40% {
    transform: rotate(-5deg);
  }
  50% {
    transform: rotate(0deg);
  }
  /* 60% {
    transform: rotate(0deg);
  } */
  100% {
    transform: rotate(0deg);
  }
}

.p-container p {
  word-break: keep-all;
}

